<template>
<SideNav>
  <div class="row">
    <div class="col-12">
      <h4 class="fw-normal mb-3 text-secondary">Appointment Create</h4>

    </div>
  </div>
  <div class="row">
    <div class="col-3">
      <div class="card border-0 shadow-sm p-3 rounded">
          <div class="">
            <form @submit.prevent="appointmentCreate" ref="appointment">
              <div class="mb-3">
                <label class="form-label">Select Patient Status</label>
                <select  name="patient_status" class="form-select">
                  <option value="1">New</option>
                  <option value="2">Old</option>
                </select>
              </div>
              <div class="mb-3">
                <label class="form-label">Patient Name</label>
                <input type="text" class="form-control" name="patient_name">
              </div>
              <div class="mb-3">
                <label class="form-label">Phone</label>
                <input type="number" class="form-control" name="phone">
              </div>
              <div class="mb-3">
                <label class="form-label">Doctor Name</label>
                <input type="text" class="form-control" name="doctor_name">
              </div>
              <div class="mb-3">
                <label class="form-label">Date</label>
                <input type="date" class="form-control" name="date">
              </div>
              <div>
                <div class="mb-3">
                  <label class="form-label">Time</label>
                  <input type="time" class="form-control" name="time">
                </div>
                <div class="mb-3">
                  <button type="submit" class="btn btn-primary w-100">Save</button>
                </div>
              </div>

            </form>
          </div>
      </div>
    </div>

    <div class="col-9">
      <div class="card border-0 shadow-sm p-3 rounded">
        <FullCalendar :options="calendarOptions" />
      </div>
    </div>
  </div>
</SideNav>
</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import '@fullcalendar/core/vdom'
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import listPlugin from '@fullcalendar/list'
import interactionPlugin from '@fullcalendar/interaction'
import bootstrap5Plugin from '@fullcalendar/bootstrap5';
import 'bootstrap-icons/font/bootstrap-icons.css';
import router from "@/router";
export default {
  name: "AppointmentCreate",
  components: {FullCalendar, SideNav},
  data() {
    return {
      calendarOptions: {
        headerToolbar: {
          left: 'dayGridMonth timeGridDay listDay',
          center: 'title',
          right: 'prev today next'
        },
        buttonText:{
          today:    'Today',
          month:    'Month',
          week:     'Week',
          day:      'Day',
          list:     'List'
        },
        eventBorderColor: 'rgba(145, 85, 253, 0.16)',
        eventTextColor: '#696cff',
        eventBackgroundColor: 'rgba(145, 85, 253, 0.16)',
        eventDisplay: 'block',
        nowIndicator: 'true',


        plugins: [ dayGridPlugin, interactionPlugin , bootstrap5Plugin , timeGridPlugin , listPlugin ],
        themeSystem: 'bootstrap5',
        initialView: 'dayGridMonth',
        height : 570,
        events: [],
        eventClick: function(el) {
          let name = el.event._def.extendedProps.patient_name;
          let status = el.event._def.extendedProps.patient_status;
          let id = el.event._def.extendedProps.old_customer_id;
          let phone = el.event._def.extendedProps.phone;
          let appointment_id = el.event._def.publicId;
          // console.log(el.event._def.publicId)
          if (status === 2){
            router.push({path: "/procedureAdd/"+id, query: { id: id }})

          }else {
            router.push({path: "/createPatient",query: {name: name,phone: phone,appointment_id: appointment_id}})
          }
        },
      }
    }
  },
  methods: {
    click(){
      alert('hello')
    },
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    appointmentCreate() {
      let formData = new FormData(this.$refs.appointment)
      axios.post(this.$store.state.url+'appointment',formData)
          .then(response=> response.data.data)
          .then(data =>{
            this.alertSuccess()
            window.location.reload()

          })
          .catch(function (error) {
            console.log(error);
          });
    }
  },
  mounted() {
    axios.get(this.$store.state.url+'appointment')
        .then(response => response.data.data)
        .then(data=>{
          this.calendarOptions.events = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

