<template>
<SideNav>
  <div class="row">
    <div class="col-12">
      <h4 class="fw-normal my-3">Add Procedure</h4>
    </div>
  </div>
  <div class="row">
    <div class="col-10 m-auto">
      <div class="card rounded shadow-sm border-0 p-3">
        <form @submit.prevent="storeProcedure" ref="procedure">
          <label class="form-label">Client's Skin Type</label>
          <div class="row">
            <div class="col-6">
              <div class="d-flex mb-3 justify-content-between">
                <div v-for="skin in skins" :key="skin.id" class="form-check">
                  <input  name="skin" @click="showSkinVal" class="form-check-input" :value="skin.value" type="radio" :id="'flexRadioDefault'+skin.id">
                  <label class="form-check-label" :for="'flexRadioDefault'+skin.id">
                    {{ skin.name }}
                  </label>
                </div>
              </div>
              <div v-if="skinRadio === true" class="d-flex justify-content-between col-4 pb-0 mb-0">
                <div class="form-check">
                  <input class="form-check-input" value="fair" type="radio" name="skin_type" id="fair">
                  <label class="form-check-label" for="fair">
                    Fair
                  </label>
                </div>
                <div class="form-check">
                  <input class="form-check-input" value="unfair" type="radio" name="skin_type" id="unfair">
                  <label class="form-check-label" for="unfair">
                    Unfair
                  </label>
                </div>
              </div>
            </div>
            <div>
              <label class="form-label mb-3">Symptoms</label>
              <div class="row">
                <div class="col-6">
                  <div class="mb-3">
                    <select class="form-select" name="symptom_id" >
                      <option value="">Select Symptom</option>
                      <option :value="symptom.id" v-for="symptom in symptoms" :key="'symptom'+symptom.id">{{symptom.name}}</option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="mb-3">
                <label class="form-label mb-3">Diagnosis</label>
                <div class="row">
                  <div class="col-6">
                    <textarea name="diagnosis" rows="2" class="form-control"></textarea>
                    <input hidden type="text" name="customer_id" :value="this.$route.query.id">
                  </div>
                </div>
              </div>
              <div>
                <h5 class="fw-normal mb-3 text-center">Choose Body Parts</h5>
                <div v-if="iscard===true" class="my-3 mx-5 d-flex justify-content-between align-center">
                  <div  @click="card('Face')" class="card shadow-sm py-3" style="width: 250px;">
                    <div class="d-flex justify-start align-center">
                      <input id="face"  type="hidden"  value="face">
                      <img src="../../assets/victoriousLogo.png" style="height: 25px;" alt="">
                      <h5 class="fw-normal mb-0">
                        Face</h5>
                    </div>
                    <img src="../../assets/face.png" class="d-flex ma-auto mb-3" style="height: 200px;" alt="">

                  </div>
                  <div @click="card('Body')" class="card shadow-sm py-3" style="width: 250px;">
                    <div class="d-flex justify-start align-center">
                      <input type="hidden"  id="body" value="body">
                      <img src="../../assets/victoriousLogo.png" style="height: 25px;" alt="">
                      <h5 class="fw-normal mb-0">
                        Body</h5>
                    </div>
                    <img src="../../assets/body.png" class="d-flex ma-auto mb-3" style="height: 200px;" alt="">
                  </div>
                  <div @click="card('Body Injection')" class="card shadow-sm py-3" style="width: 250px;">
                    <div class="d-flex justify-start align-center">
                      <input id="bodyInjection" type="hidden"  value="body injection">
                      <img src="../../assets/victoriousLogo.png" style="height: 25px;" alt="">
                      <h5 class="fw-normal mb-0">
                        Body Injection</h5>
                    </div>
                    <img src="../../assets/bodyInjection.png" class="d-flex ma-auto mb-3" style="height: 200px;" alt="">
                  </div>
                </div>
              </div>

              <div v-if="iscard===false" >
                <div class="d-flex justify-content-between gap-1 align-items-center mb-3">
                  <select class="form-select" v-model="treatment" @change="CallTreatmentUnit(treatment)" name="treatment_id">
                    <option value="">Select Treatment</option>
                    <option class="form-control" v-for="treatment in treatments" :key="'treatment'+treatments.id" :value="treatment.id">{{treatment.name}}</option>
                  </select>

                  <select class="form-select" v-model="selectTreatmentUnit" @change="CallTreatmentUnitQty(selectTreatmentUnit)" name="treatment_unit_id">
                    <option class="form-control" v-for="treatment_unit in treatment_units" :key="'treatment'+treatment_unit.id" :value="treatment_unit.id">{{treatment_unit.name}}</option>
                  </select>
                  <input @keyup="calcSellingPrice" id="unit_qty" type="number" class="form-control" name="unit_qty" :value="unit_qty.unit_qty">
                  <input type="text" class="form-control" name="unit" readonly :value="unit_qty.unit">
<!--                  <select class="form-select" v-model="selectTreatmentUnitSale" @change="CallTreatmentUnitAmount(selectTreatmentUnitSale)" name="treatment_unit_time">-->
<!--                    <option value="">Select Unit Quantity</option>-->
<!--                    <option class="form-control" v-for="treatment_unit_time in treatment_unit_quantity" :key="'treatment_unit_time'+treatment_unit_time.id" :value="treatment_unit_time.unit_qty">{{treatment_unit_time.unit_qty}}</option>-->
<!--                  </select>-->
                  <input type="hidden" class="form-control" id="origin_price"  :value="unit_qty.selling_price">
                  <input type="number" class="form-control" id="selling_price" :value="unit_qty.selling_price" name="unit_selling_price">
                  <button @click="iscard=true" class="btn btn-danger btn-sm">Del</button>
                </div>

              </div>
              <div>
                <button class="btn btn-primary">Save</button>
              </div>


            </div>
          </div>
        </form>

      </div>

    </div>
  </div>
</SideNav>
</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import router from "@/router";
export default {
  name: "ProcedureAdd",
  components: {SideNav},
  data() {
    return {
      skins: [
        {id:1,name: "I",value:"I"},
        {id:2,name: "II",value:"II"},
        {id:3,name: "III",value:"III"},
        {id:4,name: "IV",value:"IV"},
        {id:5,name: "V",value:"V"},
      ],
      skinRadio: false,
      dose_qty: '',
      iscard: true,
      dose_unit: '',
      symptoms: null,
      treatments: [],
      treatment: '',
      treatment_units: null,
      selectTreatmentUnit :'',
      unit_qty :'',
      treatment_unit_amount :0,
      selectTreatmentUnitSale: '',
      treatment_unit_type: '',
    }
  },
  methods: {
    showSkinVal() {
      this.skinRadio = true
    },
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    card(v){
      this.iscard = false
      axios.get(this.$store.state.url+'treatment')
          .then( res => res.data.data)
          .then(data => {
                this.treatments = data.filter(el=>el.body_part == v)
              }
          )
          .catch(function (error) {
            console.log(error);
          });

    },
    CallTreatmentUnit(v){
      axios.get(this.$store.state.url+'treatment/'+v)
          .then( res => res.data.data)
          .then(data =>
              this.treatment_units = data.treatment_units
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    CallTreatmentUnitQty(v){
      axios.get(this.$store.state.url+'treatment_unit/'+v)
          .then( res => res.data.data)
          .then(data =>
              this.unit_qty = data
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    calcSellingPrice(){
      let unit_qty = $('#unit_qty').val();
      let origin_price = $('#origin_price').val();
      let selling_price = $('#selling_price');
      let result = unit_qty * origin_price;
      selling_price.val(result);
    },
    storeProcedure(){
      let formData = new FormData(this.$refs.procedure);
      axios.post(this.$store.state.url+'procedure',formData)
          .then( res => res.data.data)
          .then(data =>{
                this.alertSuccess()
                router.push({path: '/patientDetail/'+ this.$route.query.id, query: {id: this.$route.query.id}})
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    }
  },
  mounted() {
    axios.get(this.$store.state.url+'sympton')
        .then( res => res.data.data)
        .then(data =>
            this.symptoms = data
        )
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

