<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import router from "@/router";
export default {
  name: "PreviousEmr",
  components: {SideNav},
  data() {
    return {
      symptoms: null,
      select_symptom: [],
      patient: [],
      condition: [],
      other_condition: '',
      diagnosis: '',
      sympton: ''

    }
  },
  methods: {
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storeErm() {
      let formData = new FormData(this.$refs.erm);
      axios.post(this.$store.state.url+'previous_emr',formData)
          .then(res=>data.data)
          .then(data => {
            this.alertSuccess()
            console.log(data)

          })
          .catch(function (error) {
            console.log(error);
          });
    },
    addSymptom(v){
      let symptom = this.symptoms.filter(el=>el.id == v);
      let symptom_id = symptom[0].id
      this.select_symptom.push(symptom_id)
    },
    storePreviousEMR(){
      let previousEMR = {
        customer_id: this.$route.query.id,
        remark: this.other_condition,
        diagnosis: this.diagnosis,
        condition: this.condition,
        symptom_id: this.select_symptom,
      }
      axios.post(this.$store.state.url+'previous_emr',previousEMR)
          .then(res =>res.data)
          .then(data => {
            this.alertSuccess()
            router.push({path: '/patientDetail/' + this.$route.query.id, query: {id: this.$route.query.id}})
          })
          // .then(data=> {
          //       router.push({path: '/customerDetails/' + this.$route.query.id, query: {id: this.$route.query.id}})
          //     }
          // )
          .catch(function (error) {
            console.log(error);
          });
    }

  },
  mounted() {
    axios.get(this.$store.state.url+'customer/'+this.$route.query.id)
        .then(response => response.data.data)
        .then(data=>{
          console.log(data)
          this.patient = data
        })
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'sympton')
        .then(response => response.data.data)
        .then(data=>{
          this.symptoms = data

        })
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

