<template>
  <SideNav>
    <div class="mb-3">
      <h4 class="fw-normal text-secondary">Treatment Unit List</h4>
    </div>
    <div class="row">
      <div class="col-12">
        <div class="card rounded shadow-sm border-0 p-3">
          <Tab>
            <li class="nav-item" role="presentation">
              <button class="nav-link active" id="treatment-unit-tab" data-bs-toggle="pill" data-bs-target="#treatment-unit" type="button" role="tab" aria-controls="treatment-unit" aria-selected="true">Create Treatment Unit</button>
            </li>
            <li class="nav-item" role="presentation">
              <button class="nav-link" id="treatment-unit-list-tab" data-bs-toggle="pill" data-bs-target="#treatment-unit-list" type="button" role="tab" aria-controls="treatment-unit-list" aria-selected="false">Treatment Unit List</button>
            </li>
          </Tab>
          <div class="tab-content" id="pills-tabContent">
            <div class="tab-pane fade show active" id="treatment-unit" role="tabpanel" aria-labelledby="profit-tab">
              <div class="my-3">
                <h5 class="fw-normal mb-3">{{ treatment.name }}'s Unit</h5>
                <div class="row">
                  <div class="col-8 m-auto">
                    <form @submit.prevent="storeTreatmentUnit" ref="treatment_unit">
                      <input type="hidden" name="treatment_id" :value="this.$route.query.id">
                      <div class="mb-3">
                        <label class="form-label">Code</label>
                        <input type="text" name="code" class="form-control">
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Name</label>
                        <input type="text" name="name" class="form-control">
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Kit</label>
                        <input type="number" name="kit" class="form-control">
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Add Procedure Medicine</label>
                        <div class="d-flex gap-2 justify-content-between">
                          <select v-model="item_id" @change="showUnitQty" name="" class="form-select">
                            <option  v-for="item in items" :key="'item'+item.id" :value="item.id">{{item.code}} {{item.name}}</option>
                          </select>
                          <button @click="addMedicine(item_id)" class="btn btn-primary btn-sm">Add</button>
                        </div>
                      </div>
                      <div class="mb-3">
                        <table class="table table-bordered border-1">
                          <tbody>
                          <tr v-for="medicine in medicines">
                            <td>
                              <input type="hidden" name="medicine_id[]" :value="medicine.medicine_id">
                              <input name="medicine_name[]" type="text" class="form-control border-0" readonly :value="medicine.name">
                            </td>
                            <td style="width: 150px;">
                              <input name="medicine_qty[]" type="number" class="form-control" :value="medicine.per_unit_qty">
                            </td>
                            <td style="width: 150px;">
                              <input name="medicine_selling_price[]" type="number" class="form-control" :value="medicine.selling_price">
                            </td>
                            <td class="text-center">
                              <button @click="deleteMedicine(medicine.id)" class="btn btn-danger btn-sm">Delete</button>
                            </td>
                          </tr>
                          </tbody>
                        </table>
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Add Machine</label>
                        <div class="d-flex gap-2 justify-content-between">
                          <select v-model="machine_id"  name="" class="form-select">
                            <option  v-for="machine in machines" :key="'machine'+machine.id" :value="machine.id">{{machine.code}} {{machine.name}}</option>
                          </select>
                          <button @click="addMachine(machine_id)" class="btn btn-primary btn-sm">Add</button>
                        </div>
                      </div>

                      <div class="mb-3">
                        <table class="table table-bordered border-1">
                          <tbody>
                          <tr v-for="machine in machinery">
                            <td>
                              <input type="hidden" name="machine_id[]" :value="machine.machine_id">
                              <input name="machine_name[]" type="text" class="form-control border-0" readonly :value="machine.name">
                            </td>
                            <td style="width: 150px;">
                              <input name="machine_qty[]" type="number" class="form-control" :value="machine.per_unit_qty">
                            </td>
                            <td style="width: 150px;">
                              <input name="machine_selling_price[]" type="number" class="form-control" :value="machine.selling_price">
                            </td>
                            <td class="text-center">
                              <button @click="deleteMedicine(machine.id)" class="btn btn-danger btn-sm">Delete</button>
                            </td>
                          </tr>
                          </tbody>
                        </table>
                      </div>

                      <div class="mb-3">
                        <label class="form-label">Unit</label>
                        <select name="unit" class="form-select">
                          <option value="Unit">Unit</option>
                          <option value="Time">Time</option>
                        </select>
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Estimate Cost</label>
                        <input type="number" name="est_cost" class="form-control">
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Selling Price</label>
                        <input name="selling_price" type="number" class="form-control">
                      </div>
                      <div class="mb-3">
                        <label class="form-label">Description (Optional)</label>
                        <textarea class="form-control" name="description" rows="2"></textarea>
                      </div>
                      <div class="mb-3">
                        <button class="btn btn-primary w-100" type="submit">Save</button>
                      </div>
                    </form>
                  </div>
                </div>

              </div>
              <div>
              </div>
            </div>
            <div class="tab-pane fade " id="treatment-unit-list" role="tabpanel" aria-labelledby="loss-tab">
              <div>
                <table class="table table-borderless table-hover">
                  <thead>
                  <tr>
                    <th class="text-center text-secondary fw-normal">#</th>
                    <th class="text-center text-secondary fw-normal">Code</th>
                    <th class="text-center text-secondary fw-normal">Name</th>
                    <th class="text-center text-secondary fw-normal">Est. Cost</th>
                    <th class="text-center text-secondary fw-normal">Selling Price</th>
                    <th class="text-center text-secondary fw-normal">Kit</th>
                    <th class="text-center text-secondary fw-normal">Description</th>
                    <th class="text-center text-secondary fw-normal">Action</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr class="text-center" v-if="treatment.treatment_units" v-for="(treatment_unit,index) in treatment.treatment_units" :key="'treatment_unit'+treatment_unit.id">
                    <td>{{index + 1}}</td>
                    <td>{{treatment_unit.code}}</td>
                    <td>{{treatment_unit.name}}</td>
                    <td>{{treatment_unit.est_cost}}</td>
                    <td>{{treatment_unit.selling_price}}</td>
                    <td>{{treatment_unit.kit}}</td>
                    <td>{{treatment_unit.description}}</td>
                    <td class="d-flex gap-3 justify-content-center">
                      <button class="btn btn-warning btn-sm">
                        <i class="fa-solid fa-pen-to-square"></i>
                      </button>
                      <button @click="deleteTreatmentUnit(treatment_unit.id)" class="btn btn-danger btn-sm">
                        <i class="fa-solid fa-trash-can"></i>
                      </button>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>

  </SideNav>

</template>

<script>
import SideNav from "@/components/layout/SideNav";
import Tab from "@/components/layout/Tab";
import axios from "axios";
export default {
  name: "TreatmentUnit",
  components: {Tab, SideNav},
  data() {
    return {
      items: null,
      treatment: [],
      item: [],
      item_id: '',
      machine_id: '',
      medicines: [],
      machinery: [],
      machines: null,


    }
  },
  methods: {
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storeTreatmentUnit(){
      let formData = new FormData(this.$refs.treatment_unit);
      axios.post(this.$store.state.url+'treatment_unit',formData)
          .then(res=>res.data.data)
          .then(data=>{
            this.alertSuccess()
            window.location.reload()
          })
          .catch(function (error) {
            console.log(error);
          });
    },
    deleteTreatmentUnit(id){
      axios.delete(this.$store.state.url+'treatment_unit/'+id)
          .then(response => response.data.data)
          .then(data=> {
            this.alertSuccess()
            window.location.reload()
          })
          .catch(function (error) {
            console.log(error);
          });

    },
    addMedicine(id){
      let i = 1;
      let item = this.items.filter((el)=>el.id === id);
      if (this.medicines.length > 0){
        i = this.medicines.length +1;
      }
      let medicineObj = {
        id: i,
        medicine_id: item[0].id,
        name: item[0].name,
        per_unit_qty: item[0].per_unit_qty,
        selling_price: item[0].selling_price,
      }
      this.medicines.push(medicineObj);
    },
    addMachine(id){
      let i = 1;
      let machine = this.machines.filter((el)=>el.id === id);
      if (this.machinery.length > 0){
        i = this.machinery.length +1;
      }
      let machineObj = {
        id: i,
        machine_id: machine[0].id,
        name: machine[0].name,
        per_unit_qty: machine[0].per_unit_qty,
        selling_price: machine[0].selling_price,
      }
      this.machinery.push(machineObj);
    },
    deleteMedicine(id){
      this.medicines = this.medicines.filter(el=>el.id != id)
    },
    showUnitQty(){
      axios.get(this.$store.state.url+'counting_unit_procedure_item/'+this.item_id)
          .then(response => response.data.data)
          .then(data=>{
            this.item = data

          })
          .catch(function (error) {
            console.log(error);
          });
    }
  },
  mounted() {
    axios.get(this.$store.state.url+'treatment/'+this.$route.query.id)
        .then(response => response.data.data)
        .then(data=>{
          this.treatment = data
        })
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'counting_unit_procedure_item')
        .then(response => response.data.data)
        .then(data=>{
          this.items = data
        })
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'machinery_item')
        .then(response => response.data.data)
        .then(data=>{
          this.machines = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

