<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-8">
        <div class="mt-5 ms-5 pt-5 ps-5">
          <img src="../../assets/doctors.png" style="width: 700px;" alt="">
        </div>
      </div>
      <div class="col-4 px-0">
          <div class="bg-white shadow-sm vh-100">
            <div class="d-flex vh-100 flex-column justify-content-center">
              <h3 class="fw-normal mb-3 text-center">WELCOME</h3>
              <p class="text-center text-secondary">Hello, Greeting! Please Sign In to Your Account!</p>
              <div class="row">
                <div class="col-10 m-auto">
                  <div class="my-3">
                    <form @submit.prevent="login" ref="login">
                      <div class="mb-3">
                        <label class="form-label"> Email</label>
                        <input type="email" class="form-control" name="email">
                      </div>
                      <div class="mb-5">
                        <label class="form-label"> Password</label>
                        <input type="password" class="form-control" name="password">
                      </div>
                      <div class="mb-3">
                        <button type="submit" class="btn btn-primary w-100">Login</button>
                      </div>
                    </form>

                  </div>
                </div>
              </div>
            </div>


        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import router from "@/router";

export default {
  name: "Login",
  data() {
    return {

    }
  },
  methods: {
    login() {
     let formData = new FormData(this.$refs.login)
      axios.post(this.$store.state.url+'login',formData)
          .then((res)=>res.data)
          .then(data=>{
            sessionStorage.setItem('Token',data.data)
            sessionStorage.setItem('Role',data.user.role)
            if (sessionStorage.getItem('Token') == data.data){
              router.push({path: '/category'})
            }

          })

          .catch(function (error) {
            console.log(error);
          });
    }
  },
}
</script>

