<template>
  <SideNav>
    <div class="my-3">
      <h4 class="fw-normal">Symptom List</h4>
    </div>
    <div class="row">
      <div class="col-8">
        <div class="card rounded shadow-sm border-0 p-3">
          <table class="table table-borderless table-hover">
            <thead>
            <tr>
              <th class="text-center text-secondary fw-normal">#</th>
              <th class="text-center text-secondary fw-normal">Symptom Type</th>
              <th class="text-center text-secondary fw-normal">Symptom Name</th>
              <th class="text-center text-secondary fw-normal">Symptom Condition</th>
              <th class="text-center text-secondary fw-normal">Description</th>
              <th class="text-center text-secondary fw-normal">Action</th>
            </tr>
            </thead>
            <tbody>
            <tr class="text-center" v-for="(symptom,index) in symptoms" :key="'symptom'+symptom.id">
              <td>{{index + 1}}</td>
              <td>{{symptom.type}}</td>
              <td>{{symptom.name}}</td>
              <td>{{symptom.condition}}</td>
              <td>{{symptom.description}}</td>
              <td class="d-flex justify-content-center gap-2">
                <button class="btn btn-sm btn-warning" @click="edit(symptom.id)">Edit</button>
                <button class="btn btn-sm btn-danger" @click="deleteCat(symptom.id)">Delete</button>
              </td>

            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="col-4">
        <!--        Create Category-->
        <div v-show="status == 'store'" class="card rounded shadow-sm border-0 p-3">
          <form @submit="storeSymptom" ref="symptom">
            <h4>Create Symptom</h4>
            <div class="mb-3">
              <label class="form-label">Symptom Type</label>
              <input type="text" name="type" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Symptom Name</label>
              <input type="text" name="name" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Condition</label>
              <input type="text" name="condition" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Description</label>
              <textarea class="form-control" name="description" rows="2"></textarea>
            </div>
            <div class="mb-3">
              <button type="submit" class="btn btn-primary w-100">Save</button>
            </div>
          </form>
        </div>

        <!--        Edit Category-->
        <div v-if="editCategory" v-show="status == 'edit'" class="card rounded shadow-sm border-0 p-3">
          <form @submit="update(editCategory.id)" ref="category">
            <h4>Edit Category</h4>
            <div class="mb-3">
              <label class="form-label">Code</label>
              <input type="text" name="code" class="form-control" :value="editCategory.code">
            </div>
            <div class="mb-3">
              <label class="form-label">Name</label>
              <input type="text" name="name" class="form-control" :value="editCategory.name">
            </div>
            <div class="mb-3">
              <label class="form-label">Description</label>
              <textarea class="form-control" name="description" rows="2" :value="editCategory.description"></textarea>
            </div>
            <div class="mb-3 d-flex justify-content-center gap-3">
              <button class="btn btn-danger" @click="status = 'store'">Cancel</button>
              <button class="btn btn-primary" type="submit" >Update</button>
            </div>
          </form>
        </div>
      </div>
    </div>

  </SideNav>

</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
export default {
  name: "Symptom",
  components: {SideNav},
  data() {
    return {
      status: 'store',
      symptoms: null,
    }
  },
  methods: {
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storeSymptom() {
      let formData = new FormData(this.$refs.symptom);
      axios.post(this.$store.state.url+'sympton',formData)
          .then(res=>res.data.data)
          .then(data=>{
            this.alertSuccess()
            window.location.reload()
          })
          .catch(function (error) {
            console.log(error);
          });
    },
    edit(id){
      axios.get(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=>{
            console.log(data)
            this.editCategory = data
          })
          .catch(function (error) {
            console.log(error);
          });
      this.status = 'edit'
    },
    update(id){
      let formData = new FormData(this.$refs.service);
      formData.append('_method','PATCH')

      axios.post(this.$store.state.url+'category/'+id,formData
      )
          .then( res => {
            res.data.data
          })
          .then(data =>{

              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    deleteCat(id){
      axios.delete(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=> {
            this.alertSuccess()
            this.categories = this.categories.filter(el=>el.id != id)
          })
          .catch(function (error) {
            console.log(error);
          });

    },
  },
  mounted() {
    axios.get(this.$store.state.url+'sympton')
        .then(response => response.data.data)
        .then(data=>{
          this.symptoms = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }

}
</script>

