<template>
  <SideNav>
    <div class="mb-3">
      <h4 class="fw-normal text-secondary">Category List</h4>
    </div>
    <div class="row">
      <div class="col-8">
        <div class="card rounded shadow-sm border-0 p-3">
          <table class="table table-borderless table-hover">
            <thead>
            <tr>
              <th class="text-center text-secondary fw-normal">#</th>
              <th class="text-center text-secondary fw-normal">Code</th>
              <th class="text-center text-secondary fw-normal">Name</th>
              <th class="text-center text-secondary fw-normal">Description</th>
              <th class="text-center text-secondary fw-normal">Action</th>
            </tr>
            </thead>
            <tbody>
            <tr class="" v-for="(category,index) in categories" :key="'category'+category.id">
              <td>{{index + 1}}</td>
              <td>{{category.code}}</td>
              <td>{{category.name}}</td>
              <td>{{category.description}}</td>
              <td class="d-flex justify-content-center gap-2">
                <button class="btn btn-sm btn-warning" @click="edit(category.id)">
<!--                  <i class="fa-solid fa-pen"></i>-->
                  <i class="fa-solid fa-pen-to-square"></i>
                </button>
                <button class="btn btn-sm btn-danger" @click="deleteCat(category.id)">
                  <i class="fa-solid fa-trash-can"></i>
                </button>
              </td>

            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="col-4">
<!--        Create Category-->
        <div v-show="status == 'store'" class="card rounded shadow-sm border-0 p-3">
          <form @submit.prevent="storeCategory" ref="category">
            <h4 class="fw-normal text-secondary">Create Category</h4>
            <div class="mb-3">
              <label class="form-label">Code</label>
              <input type="text" name="code" class="form-control">
              <div v-if="errors">
                <p class="text-danger my-3" >{{errors.code[0]}}</p>
              </div>
            </div>
            <div class="mb-3">
              <label class="form-label">Name</label>
              <input type="text" name="name" class="form-control">
              <div v-if="errors">
                <p class="text-danger my-3" >{{errors.name[0]}}</p>
              </div>

            </div>
            <div class="mb-3">
              <label class="form-label">Description</label>
              <textarea class="form-control" name="description" rows="2"></textarea>
            </div>
            <div class="mb-3">
              <Button type="submit" color="primary" size="w-100" name="Save"></Button>
            </div>
          </form>
        </div>

<!--        Edit Category-->
        <div v-if="editCategory" v-show="status == 'edit'" class="card rounded shadow-sm border-0 p-3">
          <form @submit="update(editCategory.id)" ref="category">
            <h4 class="fw-normal text-secondary">Edit Category</h4>
            <div class="mb-3">
              <label class="form-label">Code</label>
              <input type="text" name="code" class="form-control" :value="editCategory.code">
            </div>
            <div class="mb-3">
              <label class="form-label">Name</label>
              <input type="text" name="name" class="form-control" :value="editCategory.name">
            </div>
            <div class="mb-3">
              <label class="form-label">Description</label>
              <textarea class="form-control" name="description" rows="2" :value="editCategory.description"></textarea>
            </div>
            <div class="mb-3 d-flex justify-content-center gap-3">
              <button class="btn btn-danger" @click="status = 'store'">Cancel</button>
              <button class="btn btn-primary" type="submit" >Update</button>
            </div>
          </form>
        </div>
      </div>
    </div>

  </SideNav>
</template>

<script>
import SideNav from "@/components/layout/SideNav";
import Button from "@/components/layout/Button";
import axios from "axios";
import {ref} from "vue";
export default {
  name: "Category",
  components: {Button, SideNav},
  data() {
    return {
      categories : null,
      status : 'store',
      editCategory : null,
      errors : null
    }
  },
  methods: {
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storeCategory() {
      let formData = new FormData(this.$refs.category);
      axios.post(this.$store.state.url+'category',formData)
          .then(res=>res.data.data)
          .then(data=>{
            this.alertSuccess()
            window.location.reload()
          })
          .catch(err => {
            this.errors = err.response.data.errors
          });
    },
    edit(id){
      axios.get(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=>{
            console.log(data)
            this.editCategory = data
          })
          .catch(function (error) {
            console.log(error);
          });
      this.status = 'edit'
    },
    update(id){
      let formData = new FormData(this.$refs.category);
      formData.append('_method','PATCH')

      axios.post(this.$store.state.url+'category/'+id,formData
      )
          .then( res => {
            res.data.data
          })
          .then(data =>{

              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    deleteCat(id){
      axios.delete(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=> {
            this.alertSuccess()
            this.categories = this.categories.filter(el=>el.id != id)
          })
          .catch(function (error) {
            console.log(error);
          });

    },
  },
  mounted() {
    axios.get(this.$store.state.url+'category')
        .then(response => response.data.data)
        .then(data=>{
          this.categories = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }

}
</script>

