<script>

import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import Tab from "@/components/layout/Tab";
import 'bootstrap-print-css/css/bootstrap-print.css'


export default {
  name: "ProcedurePayment",
  components: {Tab, SideNav},
  data() {
    return {
      procedure_treatment: [],
      customers: null,
      procedureList: [],
      treatments: [],
      treatment_units: null,
      payments: null,
      pay_amount: 0,
      output: null,
      procedure_id: '',
    }
  },
  methods: {
    print(){
      // var divContents = document.getElementById("mydiv");
      var a = window.open('', '', 'height=500, width=500');
      a.document.write(`<html><head><link rel="stylesheet" type="text/css" href="node_modules/bootstrap-print-css/css/bootstrap-print.css" media="print"></head>`);
      a.document.write(`<body >`);
      a.document.write(`                <div class="col-10 m-auto" id="printVoucher">
                  <h3 class="text-center text-secondary mb-3">INVOICE</h3>
                  <div class="d-flex justify-content-between">
                    <table class="table table-borderless">
                      <tbody>
                      <tr>
                        <td>Customer ID</td>
                        <td>{{procedure_treatment.patientId }}</td>
                      </tr>
                      <tr>
                        <td>Customer Name</td>
                        <td>{{procedure_treatment.patient_name}}</td>

                      </tr>

                      </tbody>

                    </table>
                    <table class="table table-borderless">
                      <tbody>
                      <tr>
                        <td>Phone</td>
                        <td>{{procedure_treatment.patient_phone}}</td>

                      </tr>
                      <tr>
                        <td>Address</td>
                        <td>{{procedure_treatment.patient_address}}</td>
                      </tr>

                      </tbody>

                    </table>
                  </div>
                  <div>
                    <h4 class="fw-normal">Procedure</h4>
                    <div class="row">
                      <div class="col-8">
                        <table class="table table-borderless">
                          <tbody>
                          <tr>
                            <td>Treatment Name</td>
                            <td>{{procedure_treatment.treatment_unit_name}}</td>
                          </tr>
                          <tr>
                            <td>Procedure Price</td>
                            <td>{{procedure_treatment.procedure_price}} MMK</td>
                          </tr>
                          <tr>
                            <td>Procedure Time</td>
                            <td>{{procedure_treatment.procedure_time}}</td>
                          </tr>
                          </tbody>
                        </table>

                      </div>

                    </div>


                  </div>

                  <div>
                    <table class="table table-bordered">
                      <thead>
                      <tr>
                        <th class="text-secondary text-center fw-normal">No</th>
                        <th class="text-secondary text-center fw-normal">Description</th>
                        <th class="text-secondary text-center fw-normal">Qty</th>
                        <th class="text-secondary text-center fw-normal">Price</th>
                        <th class="text-secondary text-center fw-normal">Discount</th>
                        <th class="text-secondary text-center fw-normal">Amount</th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr class="text-center" v-for="(medicine,index) in procedure_treatment.medicines">
                        <td>{{index + 1}}</td>
                        <td>{{medicine.name}}</td>
                        <td>{{medicine.total_qty}}</td>
                        <td>{{medicine.price}}</td>
                        <td></td>
                        <td>{{medicine.total_price}}</td>
                      </tr>
                      <tr class="text-center">
                        <td class="text-end" colspan="5">TOTAL</td>
                        <td>{{procedure_treatment.total_amount}}</td>
                      </tr>
                      <tr class="text-center">
                        <td class="text-end" colspan="5">ADVANCE</td>
                        <td>{{procedure_treatment.advance}}</td>
                      </tr>
                      <tr class="text-center">
                        <td class="text-end" colspan="5">BALANCE</td>
                        <td>{{procedure_treatment.balance}}</td>
                      </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
`);
      a.document.write(`</body></html>`);
      a.document.close();
      a.print();
    },
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    calcCollectAmount(val){
      let value = parseInt(val);
      let total = parseInt($('#total_charges').val());
      total -= value;
      let result = total
      $('#collect_amount').val(result);
    },
    storePayment() {
      let formData = new FormData(this.$refs.payment);
      axios.post(this.$store.state.url+'transaction',formData)
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
                this.procedure_treatment.push(data.procedureTreatment)
                this.payments.push(data.transaction)
              }
          )
          .catch(function (error) {
            console.log(error);
          });

    }
  },
  mounted() {
    axios.get(this.$store.state.url+'procedure_treatment/'+this.$route.query.id)
        .then( res => res.data.data)
        .then(data => {
              this.procedure_treatment = data
          this.procedure_id = data.procedure_id
            }
        )
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'procedure')
        .then( res => res.data.data)
        .then(data =>{
              this.procedureList = data

        }

        )
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'treatment')
        .then( res => res.data.data)
        .then(data =>
            this.treatments = data
        )
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'treatment_unit')
        .then( res => res.data.data)
        .then(data =>
            this.treatment_units = data
        )
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'customer')
        .then( res => res.data.data)
        .then(data =>
            this.customers = data
        )
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'transaction')
        .then( res => res.data.data)
        .then(data =>
            this.payments = data
        )
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

