<template>
<SideNav>
  <div class="my-3">
    <h4 class="fw-normal">Stock Count & Price</h4>
  </div>
  <div class="row">
    <div class="col-12 ma-auto">
      <div class="my-2">
        <div class="card border-0 p-3 shadow-sm rounded-lg">
          <Tab>
            <li class="nav-item" role="presentation">
              <button class="nav-link active" id="medicine-tab" data-bs-toggle="pill" data-bs-target="#medicine" type="button" role="tab" aria-controls="medicine" aria-selected="true">Medicines</button>
            </li>
            <li class="nav-item" role="presentation">
              <button class="nav-link" id="procedure-tab" data-bs-toggle="pill" data-bs-target="#procedure" type="button" role="tab" aria-controls="procedure" aria-selected="false">Procedure Items</button>
            </li>
          </Tab>
          <div class="tab-content" id="pills-tabContent">
            <div class="tab-pane fade show active" id="medicine" role="tabpanel" aria-labelledby="medicine-tab">
              <table class="table table-hover table-borderless">
                <thead>
                <tr>
                  <th class="fw-normal text-secondary text-center">No.</th>
                  <th class="fw-normal text-secondary text-center">Code</th>
                  <th class="fw-normal text-secondary text-center">Name</th>
                  <th class="fw-normal text-secondary text-center">Current Qty</th>
                  <th class="fw-normal text-secondary text-center">From Unit</th>
                  <th class="fw-normal text-secondary text-center">To Unit</th>
                  <th class="fw-normal text-secondary text-center">Unit Convention</th>
                  <th class="fw-normal text-secondary text-center">Sale Price</th>
                  <th class="fw-normal text-secondary text-center">Purchase Price</th>
                  <th class="fw-normal text-secondary text-center">Action</th>
                </tr>
                </thead>
                <tbody>
                <tr class="text-center" v-for="(item,index) in items" :key="'item'+item.id">
                  <td>{{index + 1}}</td>
                  <td>{{item.code}}</td>
                  <td>{{item.name}}</td>
                  <td style="width: 100px;">
                    <input type="number" :id="'current_qty'+item.id" class="form-control form-control-sm" @change="changeStock(item.id)" :value="item.current_qty">
                  </td>
                  <td style="width: 100px;">
                    <input type="number" :id="'from_unit'+item.id" class="form-control form-control-sm" @change="changeFromUnit(item.id)" :value="item.from_unit">
                  </td>
                  <td style="width: 100px;">
                    <input type="number" :id="'to_unit'+item.id" @change="changeToUnit(item.id)" class="form-control form-control-sm" :value="Math.floor(item.to_unit)">
                  </td>
                  <td style="width: 100px;">
                    <input type="number" class="form-control form-control-sm" :value="item.current_qty * item.to_unit">
                  </td>
                  <td style="width: 150px;">
                    <input type="number" :id="'selling_price'+item.id" class="form-control form-control-sm" @change="changeSellingPrice(item.id)" :value="item.selling_price">
                  </td>
                  <td style="width: 150px;">
                    <input type="number" :id="'purchase_price'+item.id" class="form-control form-control-sm" @change="changePurchasePrice(item.id)" :value="item.purchase_price">
                  </td>
                  <td>
                    <button class="btn btn-danger btn-sm">Delete</button>
                  </td>
                </tr>
                </tbody>


              </table>
            </div>
            <div class="tab-pane fade" id="procedure" role="tabpanel" aria-labelledby="procedure-tab">
              <table class="table table-hover table-borderless">
                <thead>
                <tr>
                  <th class="fw-normal text-secondary text-center">No.</th>
                  <th class="fw-normal text-secondary text-center">Code</th>
                  <th class="fw-normal text-secondary text-center">Name</th>
                  <th class="fw-normal text-secondary text-center">Current Qty</th>
                  <th class="fw-normal text-secondary text-center">Sale Price</th>
                  <th class="fw-normal text-secondary text-center">Purchase Price</th>
                  <th class="fw-normal text-secondary text-center">Action</th>
                </tr>
                </thead>
                <tbody>
                <tr class="text-center" v-for="(procedure,index) in procedure_items" :key="'procedure'+procedure.id">
                  <td>{{index + 1}}</td>
                  <td>{{procedure.code}}</td>
                  <td>{{procedure.name}}</td>
                  <td style="width: 100px;">
                    <input type="number" :id="'procedure_current_qty'+procedure.id" class="form-control form-control-sm" @change="changeProcedureStock(procedure.id)" :value="procedure.current_quantity">
                  </td>
                  <td style="width: 150px;">
                    <input type="number" :id="'procedure_selling_price'+procedure.id" class="form-control form-control-sm" @change="changeProcedureSellingPrice(procedure.id)" :value="procedure.selling_price">
                  </td>
                  <td style="width: 150px;">
                    <input type="number" :id="'procedure_purchase_price'+procedure.id" class="form-control form-control-sm" @change="changeProcedurePurchasePrice(procedure.id)" :value="procedure.purchase_price">
                  </td>
                  <td>
                    <Button size="btn-sm" color="danger" name="Delete" type="button"></Button>
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</SideNav>
</template>

<script>
import SideNav from "@/components/layout/SideNav";
import Tab from "@/components/layout/Tab";
import axios from "axios";
export default {
  name: "StockCountPrice",
  components: {Tab, SideNav},
  data() {
    return {
      items: null,
    }
  },
  methods: {
    changeFromUnit(id) {
      let from_unit = $('#from_unit'+id).val();
      let medicine = {
        'id': id,
        'from_unit' : from_unit,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'item_unit/'+id,medicine
      )
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    changeToUnit(id) {
      let to_unit = $('#to_unit'+id).val();
      let medicine = {
        'id': id,
        'to_unit' : to_unit,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'item_unit/'+id,medicine
      )
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
                window.location.reload();

              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    changeStock(id){
      let current_qty = $('#current_qty'+id).val();
      let medicine = {
        'id': id,
        'current_qty' : current_qty,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'item_unit/'+id,medicine
      )
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
                window.location.reload();

              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    changeSellingPrice(id){
      let selling_price = $('#selling_price'+id).val();
      let medicine = {
        'id': id,
        'selling_price' : selling_price,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'item_unit/'+id,medicine
      )
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    changePurchasePrice(id){
      let purchase_price = $('#purchase_price'+id).val();
      let medicine = {
        'id': id,
        'purchase_price' : purchase_price,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'item_unit/'+id,medicine
      )
          .then( res => res.data)
          .then(data =>{
                this.alertSuccess()
                console.log(data)
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
  },
  mounted() {
    axios.get(this.$store.state.url+'item_unit')
        .then(response => response.data.data)
        .then(data=>{
          this.items = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

