<template>
  <SideNav>
    <div class="row">
      <div class="col-12">
        <h4 class="fw-normal my-3">Customer Procedure List</h4>
      </div>
    </div>
    <div class="row">
      <div class="col-10 m-auto">
        <div class="card border-0 p-3 shadow-sm">
          <table class="table table-hover table-borderless">
            <thead>
            <tr>
              <th class="text-center fw-normal text-secondary">Date</th>
<!--              <th class="text-center fw-normal text-secondary">Customer Name</th>-->
<!--              <th class="text-center fw-normal text-secondary">Phone</th>-->
<!--              <th class="text-center fw-normal text-secondary">Procedure</th>-->
              <th class="text-center fw-normal text-secondary">Patient Name</th>
              <th class="text-center fw-normal text-secondary">Total Amount</th>
              <th class="text-center fw-normal text-secondary">Advance</th>
              <th class="text-center fw-normal text-secondary">Balance</th>
              <th class="text-center fw-normal text-secondary">Remark</th>
              <th class="text-center fw-normal text-secondary">Action</th>
            </tr>
            </thead>
            <tbody>
            <tr class="text-center" v-for="(procedure_treatment,index) in procedure_treatments" :key="'procedure_treatment'+procedure_treatment.id">
              <td>{{procedure_treatment.appointment_date}}</td>
              <td>{{procedure_treatment.patient_name}}</td>
              <td>{{procedure_treatment.total_amount}}</td>
              <td>{{procedure_treatment.advance}}</td>
              <td>{{procedure_treatment.balance}}</td>
<!--              <td>{{procedure_treatment.procedure_id}}</td>-->
<!--              <td>{{procedure_treatments.find(el=>el.id == procedure_treatment.treatment_unit_id)}}</td>-->
              <td>{{procedure_treatment.remark}}</td>
              <td>
                <button @click="detail(procedure_treatment.id)" class="btn btn-primary btn-sm">Detail</button>
<!--                <Button color="primary" size="btn-sm" name="Check Detail" type="button"></Button>-->
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </SideNav>
</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import router from "@/router";
export default {
  name: "ProcedureTreatmentList",
  components: {SideNav},
  data() {
    return {
      procedure_treatments: null
    }
  },
  methods: {
  detail(id){
    router.push({path:'/procedurePayment/'+id,query: {id: id}})
  }
  },
  mounted() {
    axios.get(this.$store.state.url+'procedure_treatment')
        .then( res => res.data.data)
        .then(data => {
              // this.procedure_treatments = data.filter(el=>el.status == 'finish')
              this.procedure_treatments = data
            }
        )
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'customer')
        .then( res => res.data.data)
        .then(data => {
              this.customers = data
            }
        )
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'procedure')
        .then( res => res.data.data)
        .then(data =>
            this.procedures = data
        )
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

