<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import Tab from "@/components/layout/Tab";
import router from "@/router";
import ProcedureTreatmentList from "@/components/ProcedureTreatmentList";
export default {
  name: "PatientDetail",
  components: {ProcedureTreatmentList, Tab, SideNav},
  data() {
    return {
      patient: [],
      treatments: null,
      treatment_units: null,
      symptoms: null,
    }
  },
  methods: {
    addProcedure(id) {
      router.push({path: '/procedureAdd',query: {id: id}})
    }
  },
  mounted() {
    axios.get(this.$store.state.url+'customer/'+ this.$route.query.id)
        .then(response => response.data.data)
        .then(data=>{
          this.patient = data
        })
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'treatment')
        .then(response => response.data.data)
        .then(data=>{
          this.treatments = data
        })
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'treatment_unit')
        .then(response => response.data.data)
        .then(data=>{
          this.treatment_units = data
        })
        .catch(function (error) {
          console.log(error);
        });


    axios.get(this.$store.state.url+'sympton')
        .then(response => response.data.data)
        .then(data=>{
          this.symptoms = data
        })
        .catch(function (error) {
          console.log(error);
        });

  }
}
</script>

