<!--      >-->
        <h4 class="fw-normal text-secondary">Procedure Treatment</h4>
<!--      </v-badge>-->
      <button class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#exampleModal">Finish</button>
<!--      <v-btn type="button"  class="text-capitalize" color="success" dark>-->
      <!-- Modal -->
      <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">Notification</h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <div class="form-check mb-3">
                <input class="form-check-input" value="Include Procedure Cost" type="radio" v-model="remark" name="remark" id="flexRadioDefault1">
                <label class="form-check-label" for="flexRadioDefault1">
                  Include Procedure Cost
                </label>
              </div>
              <div class="form-check mb-3">
                <input class="form-check-input" value="Not Include Procedure Cost" type="radio" v-model="remark" name="remark" id="flexRadioDefault2">
                <label class="form-check-label" for="flexRadioDefault2">
                  Not Include Procedure Cost
                </label>
              </div>

              <div>
                <input type="hidden" name="procedure_price" :value="procedures.unit_selling_price">
              </div>
            </div>
            <div class="modal-footer">
              <button class="btn btn-danger btn-sm" data-bs-dismiss="modal">Close</button>
              <button @click="finish" type="button"  class="btn btn-primary btn-sm">Confirm</button>
<!--              <v-btn type="button" small color="error"  data-bs-dismiss="modal">Close</v-btn>-->
<!--              <v-btn  small color="success">Confirm</v-btn>-->
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>

  <div class="row">
    <div class="col-11 m-auto">
      <div class="card rounded mb-3 border-0 p-3 shadow-sm">
        <h5 class="fw-normal">Physical Examination</h5>
        <table class="table table-borderless">
          <tbody>
          <tr>
            <td>Skin</td>
            <td>{{procedures.skin}}</td>
          </tr>
          <tr>
            <td>Skin Type</td>
            <td>{{procedures.skin_type}}</td>
          </tr>
          <tr>
            <td>Diagnosis</td>
            <td>{{ procedures.diagnosis }}</td>
          </tr>
          <tr>
            <td>Treatment</td>
            <td>{{procedures.treatment_id}}</td>
          </tr>
          <tr>
