            <td>{{ procedures.status}}</td>
          </tr>
          <tr>
            <td>Treatment Amount</td>
            <td>{{ procedures.unit_selling_price}}</td>
          </tr>
          </tbody>
        </table>
      </div>

   </div>
  </div>

  <div class="row">
    <div class="col-11 m-auto">
      <div class="card rounded border-0 shadow-sm p-3">
        <form ref="procedureTreatment" @submit.prevent="" enctype="multipart/form-data">
          <div class="mb-3">
            <label class="form-label">Treatment Diagnosis</label>
            <input type="text" class="form-control" name="treatment_diagnosis" :value="treatment_diagnosis">
          </div>
