              <div class="d-flex gap-3 my-5" >
                <img style="width: 200px;object-fit: cover" class="rounded" v-for="photo in before_photo[0]" :key="'photo'+photo.id" :src="photo.photo"  alt="">
              </div>
            </div>
          </div>
          <div v-if="this.$route.query.index > 0" class="my-5">
            <h5 class="fw-normal">Before Photo</h5>
            <div>
              <input type="file" name="photos[]" class="form-control" multiple>
              <div class="d-flex gap-3 my-5" >
                <img style="width: 200px;object-fit: cover" class="rounded" v-for="photo in before_photo[0]" :key="'photo'+photo.id" :src="photo.photo"  alt="">
              </div>
            </div>
          </div>
          <div v-if="this.$route.query.index > 0" class="my-5">
            <h5 class="fw-normal">After Photo</h5>
            <div>
              <input type="file" name="after_photos[]" class="form-control" multiple>
              <div class="d-flex gap-3 my-5" >
                <img style="width: 200px;object-fit: cover" class="rounded" v-for="photo in after_photo[0]" :key="'photo'+photo.id" :src="photo.photo" alt="">
              </div>
            </div>
          </div>
          <div class="my-5 d-flex justify-content-center">
            <button type="submit" @click="storeProcedureTreatment" class="btn btn-primary">Save</button>
<!--            <v-btn type="submit"  color="success" dark>Save</v-btn>-->
          </div>
