<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import AddMedicine from "@/components/AddMedicine";
export default {
  name: "ProcedureTreatmentDetail",
  components: {AddMedicine, SideNav},
  data() {
    return {
      procedure_treatment: [],
      before_photo: [],
      after_photo: [],
      procedures: [],
      treatment_units: null,
      symptons: null,
      description: '',
      treatment_diagnosis: '',
      procedure_id: '',
      treatments: null,
      remark: '',
      medicines: null,
    }
  },
  methods: {
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storeProcedureTreatment() {
      let formData = new FormData(this.$refs.procedureTreatment);
      formData.append('_method', 'PATCH');
      axios.post(this.$store.state.url+'procedure_treatment/'+this.$route.query.id,formData
      )
          .then( res => res.data.data)
          .then(data =>{
            this.alertSuccess()
            window.location.reload()
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    finish(){

      let procedure_status = {
        'status' : 'finish',
        'remark' : this.remark,
        '_method' : 'PATCH',
      }
      axios.post(this.$store.state.url+'procedure_treatment/'+this.$route.query.id,procedure_status
      )
          .then( res => res.data.data)
          .then(data =>{
            this.alertSuccess()
              }
          )
          .catch(function (error) {
            console.log(error);
          });

    }
  },
  mounted() {
    axios.get(this.$store.state.url+'procedure_treatment/'+this.$route.query.id)
        .then( res => res.data.data)
        .then(data => {
          console.log(data)
              this.procedure_treatment = data
              this.treatment_diagnosis = data.treatment_diagnosis
              this.description = data.description
              this.medicines = data.medicines
              this.procedure_id = this.procedure_treatment.procedure_id
              let before = data.photos.filter(el => el.status == 1)
              this.before_photo.push(before)
              let after = data.photos.filter(el => el.status == 2)
              this.after_photo.push(after)
            }
        )
        .catch(function (error) {
          console.log(error);
        });


    axios.get(this.$store.state.url+'treatment_unit')
        .then( res => res.data.data)
        .then(data =>
            this.treatment_units = data
        )
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'sympton')
        .then( res => res.data.data)
        .then(data =>
            this.symptons = data
        )
        .catch(function (error) {
          console.log(error);
        });

    axios.get(this.$store.state.url+'procedure/'+this.$route.query.procedure_id)
        .then( res => res.data.data)
        .then(data => {
              this.procedures = data
            }
        )
        .catch(function (error) {
          console.log(error);
        });
    axios.get(this.$store.state.url+'treatment')
        .then( res => res.data.data)
        .then(data =>
            this.treatments = data
        )
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

