<template>
  <SideNav>
    <div class="mb-3">
      <h4 class="fw-normal text-secondary">Patient Register</h4>
    </div>
    <div class="row">
      <div class="col-10 m-auto">
        <div class="card rounded shadow-sm border-0 p-3">
          <div class="row">
            <div class="col-7">
              <div>
                <img @click="openFile" id="backgroundImg" src="../../assets/cover-image.jpeg" class="img-fluid rounded-4 shadow-sm" alt="...">
              </div>
            </div>
            <div class="col-5">
              <div>
                <form @submit.prevent="storePatient" ref="patient" enctype="multipart/form-data">
                  <input type="file" id="fileInput" class="d-none" name="photo">
                  <div class="mb-3">
                    <label class="form-label">Name</label>
                    <input type="text" class="form-control" name="name" :value="this.$route.query.name">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Age</label>
                    <input type="text" class="form-control" name="age">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Gender</label>
                    <div>
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="gender" id="inlineRadio1" value="male">
                        <label class="form-check-label" for="inlineRadio1">Male</label>
                      </div>
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="gender" id="inlineRadio2" value="female">
                        <label class="form-check-label" for="inlineRadio2">Female</label>
                      </div>
                    </div>
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Date of Birth</label>
                    <input type="date" class="form-control" name="date_of_birth">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Occupation</label>
                    <input type="text" class="form-control" name="occupation">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Email (Optional)</label>
                    <input type="email" class="form-control" name="email">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Phone</label>
                    <input type="number" class="form-control" name="phone" :value="this.$route.query.phone">
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Address</label>
                    <textarea class="form-control" name="address" rows="1"></textarea>
                  </div>
                  <div class="mb-3">
                    <label class="form-label">Drug Allergy (Optional)</label>
                    <textarea class="form-control" name="drug_allergy" rows="1"></textarea>
                  </div>
                  <div class="mb-3">
                    <button class="btn btn-primary w-100">Register</button>
                  </div>


                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </SideNav>

</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import router from "@/router";
export default {
  name: "CreatePatient",
  components: {SideNav},
  data() {
    return {
      photo : '',
    }
  },
  methods: {
    openFile() {
      let fileInput = document.getElementById('fileInput');
      let backgroundImg = document.getElementById("backgroundImg");
      fileInput.click();
      fileInput.addEventListener('change', function () {
        let file = fileInput.files[0]
        this.photo = file
        console.log(this.photo.name)

        const reader = new FileReader();
        reader.onload = function () {
          backgroundImg.src = reader.result;
          this.photo = reader.result
        }
        reader.readAsDataURL(file)
      })
    },
    alertSuccess(){
      this.$swal({
        toast: true,
        position: 'top-end',
        icon: 'success',
        timerProgressBar: true,
        showConfirmButton: false,
        timer: 3000,
        title: 'Success',
      });
    },
    storePatient() {
      let formData = new FormData(this.$refs.patient);
      axios.post(this.$store.state.url+'customer',formData)
          .then( res => res.data.data)
          .then(data =>{
                this.alertSuccess()
                router.push({path: '/previousEmr/'+data.id,query: {id: data.id}})
              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    edit(id){
      axios.get(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=>{
            console.log(data)
            this.editCategory = data
          })
          .catch(function (error) {
            console.log(error);
          });
      this.status = 'edit'
    },
    update(id){
      let formData = new FormData(this.$refs.service);
      formData.append('_method','PATCH')

      axios.post(this.$store.state.url+'category/'+id,formData
      )
          .then( res => {
            res.data.data
          })
          .then(data =>{

              }
          )
          .catch(function (error) {
            console.log(error);
          });
    },
    deleteCat(id){
      axios.delete(this.$store.state.url+'category/'+id)
          .then(response => response.data.data)
          .then(data=> {
            this.alertSuccess()
            this.categories = this.categories.filter(el=>el.id != id)
          })
          .catch(function (error) {
            console.log(error);
          });

    },
  },
}
</script>

