<template>
  <SideNav>
    <div class="my-3">
      <h4 class="fw-normal">Patient List</h4>
    </div>
    <div class="row">
      <div class="col-11 m-auto">
        <div class="card rounded shadow-sm border-0 p-3">
          <div class="table-responsive">
            <table style="width: 1000px;" class="table table-borderless table-hover">
              <thead>
              <tr>
                <!--              <th class="text-center"></th>-->
                <th class="text-center text-secondary fw-normal">#</th>
                <th class="text-center text-secondary fw-normal">Name</th>
                <th class="text-center text-secondary fw-normal">Age</th>
                <th class="text-center text-secondary fw-normal">Phone</th>
                <th class="text-center text-secondary fw-normal">Date of Birth</th>
                <th class="text-center text-secondary fw-normal">Gender</th>
                <th class="text-center text-secondary fw-normal">Email</th>
                <th class="text-center text-secondary fw-normal">Address</th>
                <th class="text-center text-secondary fw-normal">Action</th>
              </tr>
              </thead>
              <tbody>
              <tr class="text-center" v-for="(patient,index) in patients" :key="'patient'+patient.id">
                <!--              <td>-->
                <!--                <img :src="patient.photo" style="width:50px;height:50px;object-fit: cover;" class="rounded-circle" alt="">-->
                <!--              </td>-->
                <td>{{index +1}}</td>
                <td>{{patient.name}}</td>
                <td>{{patient.age}}</td>
                <td>{{patient.phone}}</td>
                <td>{{patient.date_of_birth}}</td>
                <td>{{patient.gender}}</td>
                <td>{{patient.email}}</td>
                <td>{{patient.address}}</td>
                <td>
                  <button @click="detail(patient.id)" class="btn btn-primary btn-sm">Detail</button>
                </td>
              </tr>
              </tbody>
            </table>

          </div>
        </div>
      </div>
    </div>

  </SideNav>

</template>

<script>
import SideNav from "@/components/layout/SideNav";
import axios from "axios";
import router from "@/router";
export default {
  name: "PatientList",
  components: {SideNav},
  data() {
    return {
      patients: null,
    }
  },
  methods: {
    detail(id){
      router.push({path: '/patientDetail/'+id,query:{id: id}})
    }
  },
  mounted() {
    axios.get(this.$store.state.url+'customer')
        .then(response => response.data.data)
        .then(data=>{
          this.patients = data
        })
        .catch(function (error) {
          console.log(error);
        });
  }
}
</script>

